import React from "react";
import PropTypes from "prop-types";

const google = window.google;
export default class Map extends React.Component {
  componentDidMount() {
    this.loadMap();
  }

  loadMap() {
    new google.maps.StreetViewPanorama(this.refs.map, {
      position: {
        lat: this.props.initialPosition.lat,
        lng: this.props.initialPosition.lng,
      },
      pov: {
        heading: 180,
        pitch: 0,
      },
      fullscreenControl: false,
      addressControl: false,
      enableCloseButton: false,
    });
  }
  render() {
    return <div id="map" ref="map" className="streetview-map"></div>;
  }
}

Map.propTypes = {
  initialPosition: PropTypes.object.isRequired,
};
