import React from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import * as compsActions from "../../actions/compsActions";
import { Navigation } from "../../lib";
import Map from "./StreetViewMap";
import getParameterByName from "../common/getParameterByName";

class CompSVContainer extends React.Component {
  render() {
    return (
      <div className="comps-page">
        <div className="comps-street-sub-header">
          <div className="comps-back-arrow" onClick={Navigation.goBack}>
            <img src="/images/icons/arrow-left.png" alt="arrow-left" />
          </div>
          <div className="address-title">{getParameterByName("address")}</div>
        </div>
        <Map
          initialPosition={{
            lat: parseFloat(getParameterByName("latitude")),
            lng: parseFloat(getParameterByName("longitude")),
          }}
        />
      </div>
    );
  }
}
function mapStateToProps(state) {
  return {
    comps: state.comps,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(compsActions, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(CompSVContainer);
